import {Button,Form} from 'react-bootstrap'
import {Link, useNavigate} from  'react-router-dom';
import * as Yup from 'yup';
import { Formik} from 'formik';
import '../../styles/form.css';
import Axios from "axios"
import { useState } from 'react';

const SingUpForm =() => {

  const navigate = useNavigate()
  const [errorsSubmit, setErrorsSubmit] = useState({})

  const schema = Yup.object().shape({
    email: Yup.string().email('El valor ingresado no es un email').required('el email es obligatorio').trim('El email no permite espacios en blanco'),
    password: Yup.string()
      .required('La contrasea es obligatoria.') 
      .min(8, 'La contrasea debe tener mnimo 8 caracteres'),
    confir:Yup.string().oneOf([Yup.ref('password'), null], 'las contrasenas no son iguales')
      .required('La contrasea es obligatoria.') 
      .min(8, 'La contrasea debe tener mnimo 8 caracteres'),
  });

  return (
    <Formik    
        validationSchema={schema}
            initialValues={{
                email:'',
                password:'',
                confir:'',
            }}
            onSubmit={values => {
              // VALIDAR AC si es unsl.edu.ar o email.unsl.edu.ar
              Axios.post("api/register",{
                "email": values.email,
                "password": values.password,
                "confirm_password": values.confir,
              })
              .then(response => {
                console.log(response)
                navigate("/registro-exitoso")
              })
              .catch(error => {
                setErrorsSubmit({})
                console.log(error)
                setErrorsSubmit(error.response.data.errors)
              })
            }}
            >
            {({handleChange, handleSubmit, handleBlur, values ,touched , errors}) => (
              <Form className="form" >
                <h1 className="encabezado-3 title">Registrarse</h1>
                <Form.Group className="mb-3" controlId="CorreoSingUp" >
                  <Form.Label className="encabezado-4 label" >Correo Electrnico:</Form.Label>
                  <p className="text-5 label-secundary">debe ser de formato unsl.edu.ar o email.unsl.edu.ar</p>
                  <Form.Control  
                      type="email"
                      name='email'
                      onChange={handleChange} 
                      onBlur={handleBlur} 
                      value={values.email} 
                      isValid={touched.email && !errors.email}
                      isInvalid={touched.email && errors.email}
                    />
                    <Form.Control.Feedback type='invalid'>
                        {errors.email }
                    </Form.Control.Feedback>
                    {
                      errorsSubmit.email ? <p className='error-submit'>{errorsSubmit.email}</p> : ""
                    }
                </Form.Group>    
                <Form.Group className="mb-3" controlId="ContraseaSingUp" >
                  <Form.Label className="encabezado-4 label">Contrasea:</Form.Label>
                  <p className="text-5 label-secundary">debe ser de mnimo 8 caracteres</p> 
                  <Form.Control 
                        type="password" 
                        name='password' 
                        onChange={handleChange}
                        onBlur={handleBlur}
                        value={values.password}
                        isValid={touched.password && !errors.password}
                        isInvalid={touched.password && errors.password}
                    />
                    <Form.Control.Feedback type='invalid'>
                        {errors.password }
                    </Form.Control.Feedback>
                    {
                      errorsSubmit.password ? <p>{errorsSubmit.password}</p> : ""
                    }
                </Form.Group>
                  <Form.Group className="mb-3" controlId="contraseaconfig2" >
                  <Form.Label className="encabezado-4 label">Confirmar contrasea:</Form.Label>
                  <Form.Control className='mb-4'
                        type="password" 
                        name='confir' 
                        onBlur={handleBlur}
                        onChange={handleChange} 
                        value={values.confir}
                        isValid={touched.confir && !errors.confir}
                        isInvalid={touched.confir && errors.confir}
                    />
                    <Form.Control.Feedback type='invalid'>
                        {errors.confir }
                    </Form.Control.Feedback>
                  <Link to="/LoginPage" className="text-5 link">Ya tienes una cuenta? Iniciar Sesin</Link>
                </Form.Group>
                <Button className="btn btn-form" type='submit' onClick={handleSubmit} value='Enviar'>Registrarse</Button>
              </Form>
               )}
      </Formik>
  );
}

export default SingUpForm;