import Form from 'react-bootstrap/Form'
import Button from 'react-bootstrap/Button'
import {Link} from  'react-router-dom';
import * as Yup from 'yup';
import { Formik} from 'formik';
import '../../styles/form.css';
import { useNavigate } from 'react-router-dom';
import Axios from 'axios';
import { useState } from 'react';

function LoginForm() {

    const navigate = useNavigate();
    const [errorSubmit, setErrorSubmit] = useState("")
      const schema = Yup.object().shape({
        email: Yup.string()
          .email('El valor ingresado no es un email')
          .required('el email es obligatorio')
          .trim('El email no permite espacios en blanco'),
        password: Yup.string()
          .required('La contrasea es obligatoria.')
          .min(8, 'La contrasea debe tener mnimo 8 caracteres')
      });

  return(
    <Formik    
      validationSchema={schema}
          initialValues={{
              email:'',
              password:'',
          } }
          onSubmit={values => {
            Axios.post('/api/login', {
            "email": values.email,
            "password": values.password
        })
        .then(response => {
          console.log(response);
          localStorage.setItem('local-email', values.email);
          localStorage.setItem('local-token', response.data.token);
          navigate("/")
        })
        .catch(function (error) {
          console.log(error.response.data);
          setErrorSubmit(error.response.data.message)
        }) 
        }}
      >
      {({handleChange, handleSubmit, handleBlur, values ,touched , errors}) => (
        <Form onSubmit={handleSubmit} className="form">
          <h1 className='encabezado-3 title'>Iniciar Sesin</h1>
          <Form.Group className="mb-3" controlId="correologin1" >
            <Form.Label className="encabezado-4 label">Correo Electrnico:</Form.Label>
            <p className="text-5 label-secundary">debe ser de formato unsl.edu.ar o email.unsl.edu.ar</p>
            <Form.Control 
              name='email'
              type="text"
              value={values.email}
              onBlur={handleBlur}
              onChange={handleChange}
              isValid={touched.email && !errors.email}
              isInvalid={touched.email && errors.email}
              />
              <Form.Control.Feedback type='invalid'>
                  {errors.email}
              </Form.Control.Feedback>
              
          </Form.Group>
          <Form.Group className="mb-3" controlId="contrasealogin1" >
              <Form.Label className="encabezado-4 label">Contrasea:</Form.Label>
              <p className="text-5 label-secundary">debe ser de mnimo de 8 caracteres</p>
              <Form.Control className='mb-4'
                  name='password'
                  type="password"
                  value={values.password}
                  onChange={handleChange}
                  onBlur={handleBlur}
                  isValid={touched.password && !errors.password}
                  isInvalid={touched.password && errors.password}
                  />
                  <Form.Control.Feedback type='invalid'>
                      {errors.password }
                  </Form.Control.Feedback>
              <Link to="/SingUpPage" className="text-5 link" >No tienes una cuenta? Registrarse</Link>
          </Form.Group>
          <Button className="btn btn-form "onClick={handleSubmit}>Ingresar</Button>
          {errorSubmit ? <p className="error-submit"> {errorSubmit}</p> : ""}
          <Link to="/RestablecerPage" className="text-5 link" >Olvid mi contrasea</Link>
        </Form>
      )}
  </Formik>
  )
}

export default LoginForm;